import React from "react";
import { HashRouter as Router, Route } from "react-router-dom";
import TopPage from "./pages/top.js";
import BeltGuidePage from "./pages/belt-guide.js";
import ZigenBeltPage from "./pages/zigen-belt";
import AdaBeltPage from "./pages/ada-belt";
import RahBeltPage from "./pages/rah-belt";
import MamadouBeltPage from "./pages/mamadou-belt";
import LevelOnePage from "./pages/level-one";

import HtmlExcersices1 from "./pages/level1/html";
import SimpleProfile from "./pages/level1/html/simple-profile";
import HamburgerShop from "./pages/level1/html/hamburger-shop";

import JsExcersices1 from "./pages/level1/js";
import HelloAlert from "./pages/level1/js/hello-alert";
import ChangeColor from "./pages/level1/js/change-color";

import GitExcersices1 from "./pages/level1/git";

import SchemeExcersices1 from "./pages/level1/scheme";

function App() {
  return (
    <Router>
      <Route path="/" exact component={TopPage} />
      <Route path="/belt-guide" component={BeltGuidePage} />
      <Route path="/zigen-belt" component={ZigenBeltPage} />
      <Route path="/ada-belt" component={AdaBeltPage} />
      <Route path="/rah-belt" component={RahBeltPage} />
      <Route path="/mamadou-belt" component={MamadouBeltPage} />
      <Route path="/level-one" component={LevelOnePage} />
      <Route path="/level1/html" component={HtmlExcersices1} exact />
      <Route
        path="/level1/html/simple-profile"
        component={SimpleProfile}
        exact
      />
      <Route
        path="/level1/html/hamburger-shop"
        component={HamburgerShop}
        exact
      />

      <Route path="/level1/js" component={JsExcersices1} exact />
      <Route path="/level1/js/hello-alert" component={HelloAlert} exact />
      <Route path="/level1/js/change-color" component={ChangeColor} exact />

      <Route path="/level1/git" component={GitExcersices1} exact />
      <Route path="/level1/scheme" component={SchemeExcersices1} exact />
    </Router>
  );
}

export default App;
